import React, { useEffect, useState, useRef } from "react";
import "./Landing.css";
import Logo from "../../images/main-logo.png";
import CountUp from "react-countup";
import VisibilitySensor from "react-visibility-sensor";
import axios from "axios";

function Landing() {
  const [animate, setAnimate] = useState(false);

  const [countValue, setCount] = useState(0);

  let audio = new Audio("/audioFile.mp3");

  const handleAnimate = () => {
    setAnimate(true);
    audio.play();
    document.getElementById("tap_button").style.display = "none";
  };

  axios({
    method: "GET",
    url: "https://dts.chords.org.lk/api/count",
  }).then((res) => {
    setCount(Number(res.data.count));
  });

  useEffect(() => {
    document.getElementById("tap_button").disabled = true;
    setTimeout(() => {
      document.getElementById("tap_button").disabled = false;
    }, 3000);
  }, []);

  const slideBgRef = useRef(null);

  useEffect(() => {
    setTimeout(() => {
      console.log("Adding new class");
      slideBgRef.current.className = "slider-bg slider-bg-animate-in";
    }, 300);
  }, []);

  return (
    <>
      <div
        className={
          animate
            ? "landing__container landing__container_animation"
            : "landing__container"
        }
      >
        <div className="landing__overlay">
          <div ref={slideBgRef} className="slider-bg">
            {[
              {
                imageUrl: "landing_bg/primetuition.png",
                link: "http://primetuition.lk/index2.php",
              },
              {
                imageUrl: "landing_bg/leeds.png",
                link: "https://primetuition.lk/Hyb2T/tutory-time-table.php",
              },
              {
                imageUrl: "landing_bg/manoh.PNG",
                link: "http://manojsolangaarachchi.com",
              },
              {
                imageUrl: "landing_bg/UPUL.PNG",
                link: "http://sannasgala.chords.org.lk",
              },
              {
                imageUrl: "landing_bg/GAYAN.PNG",
                link: "http://gayaniddamalgoda.com",
              },
              {
                imageUrl: "landing_bg/IMA.PNG",
                link: "http://ima.lk",
              },
              {
                imageUrl: "landing_bg/saman.png",
                link: "http://samanedirisinghe.com",
              },
              {
                imageUrl: "landing_bg/saranga.png",
                link: "http://sarangapalihawadana.com",
              },

              {
                imageUrl: "landing_bg/supreme.PNG",
                link: "http://supremeonline.net",
              },
              {
                imageUrl: "landing_bg/ranuka.PNG",
                link: "https://ranukaperera.com",
              },
              {
                imageUrl: "landing_bg/yasas.PNG",
                link: "http://yasassenarath.com",
              },
            ].map(function (x, index) {
              console.log(index);
              return (
                <a href={x.link} target="_blank">
                  <div
                    style={{
                      gridColumnStart: index * 2 + (index % 2) == 0 ? 1 : 0,
                      gridColumnEnd: index * 2 + 1 + (index % 2) == 0 ? 1 : 0,
                      gridRowStart: (index + 1) % 2,
                      gridRowEnd: ((index + 1) % 2) + 1,
                      transform: `translate(-${index % 2 != 0 ? 0 : 50}%, 0)`,
                    }}
                  >
                    <div
                      className="slider-bg-child"
                      style={{
                        background: `url(${x.imageUrl})`,
                        backgroundSize: "cover",
                      }}
                    >
                      <span>Visit</span>
                    </div>
                  </div>
                </a>
              );
            })}
          </div>
        </div>
        <div className="landing-card">
          <div className="landing__grid-item">
            <div
              className={
                animate ? "landing__logo-animate" : "landing__logo-wrapper"
              }
            >
              <img src={Logo} alt="Logo" className="landing__logo" />
              <h1 className="landing__logo-content">
                Digital Tutorial Services
              </h1>
            </div>

            <div className="landing__count-wrapper">
              <CountUp end={countValue} redraw={true} duration={2}>
                {({ countUpRef, start }) => (
                  <VisibilitySensor onChange={start} delayedCall>
                    <span
                      className={
                        animate ? "landing__count-animate" : "landing__count"
                      }
                      ref={countUpRef}
                    />
                  </VisibilitySensor>
                )}
              </CountUp>
              <p className="landing__count-text">Dots Connected So Far</p>
            </div>
          </div>

          {/* <button
          id="tap_button"
          className="landing__button landing__button2"
          style={{
            marginBottom: "64px",
          }}
          onClick={() => {
            window.location.href = "https://primetuition.lk/2023al.php";
          }}
        >
          {" "}
          2023 A/L
        </button> */}

          <button
            id="tap_button"
            className="landing__button"
            onClick={handleAnimate}
          >
            {" "}
            Tap To Connect{" "}
          </button>

          <div className="landing__grid-item">
            <h1
              className={
                animate
                  ? "landing__bottom-text-animate"
                  : "landing__bottom-text"
              }
            >
              Connecting The Dots
            </h1>
            <p
              className={
                animate
                  ? "landing__dot-animation-animte"
                  : "landing__dot-animation"
              }
            >
              <span id="dot_1"> . </span>
              <span id="dot_2"> . </span>
              <span id="dot_3"> . </span>
              <span id="dot_4"> . </span>
              <span id="dot_5"> . </span>
            </p>
          </div>
        </div>
      </div>
    </>
  );
}

export default Landing;
